.menu {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 100px;
    font-size: 1.3rem;
    cursor: pointer;
    font-weight: 500;
}

.menuLogo {
    width: 33%;
    text-align: start;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    font-weight: 600;
}

.menuItem {
    width: 33%;
    display: flex;
    justify-content: center;
    flex-direction: row;
    gap: 30px;
    font-size: 0.9rem;
}

.menuButtonItem:hover {
  color: #F0A952;
}

.menuLogin {
    width: 33%;
    display: flex;
    justify-content: end;
    font-size: 0.9rem;
  }

.displayMenuDiv {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 20px;
    align-items: center;
}

.imgPlace {
  width: 20%;
}

.imgLogo {
  width: 80%;
}

.scoreText {
  font-size: 3rem;
  font-weight: 700;
}

.meText {
  font-size: 3rem;
  font-weight: 800;
  font-style: italic;
  background-color: #e8bd88;
  background-image: linear-gradient(45deg, #e8bd88, #eab97e, #ebb674, #edb26a, #eeae60, #f0aa56, #f2a64d, #f4a243, #f79d37, #f9982a, #fc921a, #ff8c00);
  background-size: 100%;
  background-repeat: repeat;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; 
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
}

.menuIcon {
  cursor: pointer;
}

.modalMenu {
 position: absolute;
 right: 5%;
 top: 5%;
 background-color: #ffe7c9;
 margin-top: 15px;
 border-radius: 10px;
}

.icon-cross {
  display: flex;
}

.icon-hamburger {
  display: flex;
}

.buyCredits {
  background-color: #FF8C00;
  padding: 10px;
  border-radius: 30px;
}

.buyCredits:hover {
  background-color: #F0A952
}

.menuBlock {
  display: flex;
  flex-direction: row;
  padding: 8px 15px 8px 15px;
  justify-content: space-between;
  font-size: 0.8rem;
}

.menuUpgradeButton {
  width: 180px;
  background-color: #FF8C00;
  padding: 15px;
  border-radius: 30px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 5px;
  font-size: 1rem;
}

.menuUpgradeButton:hover {
  background-color: #F0A952;
}

.authMenuButton {
  width: 100%;
}

.authMenuButton span:hover {
  color: #ffffff;
}

.photoProfile {
  display: flex;
}