.rateBody {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.rateUpper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 25px;
    margin: 25px;
}

.inputRateField {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.rateUpper input {
    height: 45px;
    width: 300px;
    font-size: 1.2rem;
    text-align: center;
    border-radius: 6px 0px 0px 6px;
    border: 1px solid;
}

.rateResponse {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.rateResponseImage {
    width: 60%;
    margin: 10px;
    overflow-y: auto;
    max-height: 75vh;
}

.rateResponseText {
    width: 40%;
    margin: 10px;
    overflow-y: auto;
    max-height: 75vh;
}

.criteriaList {
    display: flex;
    flex-direction: row;
    gap: 10px;
    flex-wrap: wrap;
    padding: 0;
    width: 60%;
}

.criteriaItem {
    margin: 5px 0;
    padding: 10px;
    border: 1px solid #090806; /* Default border color */
    cursor: pointer;
    font-size: 1rem;
    border-radius: 6px;
}

.criteriaItem.selected {
    border-color: #E8BD88; /* Border color when selected */
}

.enterButton {
    height: 47px;
    border-radius: 0px 6px 6px 0px;
    font-size: 1.1rem;
    padding: 0px 5px 0px 5px;
    display: flex;
    justify-content: center;
    align-items: center;;
    background-color: #FF8C00;
    border: 1px solid;
}

.enterButton:hover {
    background-color: #E8BD88;
}

.responseBox {
    border: 1px solid #F0A952;
    margin: 20px;
    padding: 5px;
    border-radius: 6px;
    font-size: 1.2rem;
    background-color: hsl(33, 100%, 50%, 5%);
}

/* Add this to your CSS file */
.scoreStyle {
    font-weight: bold;
    color: #F0A952; /* Example color */
}

.saveThisPost {
    font-size: 1.3rem;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 95%;
    gap: 20px;
}

.saveThisButton {
    background-color: #FF8C00;
    padding: 10px 20px 10px 20px;
    border-radius: 5px;
    display: flex;
}

.saveThisButton:hover {
    background-color: #F0A952;
}
