.App {
  text-align: center;
  min-height: 100vh;
  background-color: #FAF7F5;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: #090806;
  font-family: "Poppins", sans-serif;
}

.App body {
  width: 80%;
  font-family: "Poppins", sans-serif;
}

.App footer {
  width: 90%;
  text-align: center;
  background-color: #FAF7F5;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.selectorProduct select{
  width: 400px;
  height: 50px;
  border-radius: 6px;
  text-align: center;
  font-size: 1.2rem;
}

.mainTitle {
  margin-block-end: 0px;
}

.secondTitle {
  margin-block-start: 0px;
  font-size: 1.5rem;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  width: 90%;
}

.App-link {
  color: #61dafb;
}

.productImage {
  width: 90%;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 5px;
  
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #F0A952;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #E8BD88;
}