.dashboard {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 50px;
}

.responseCard {
    display: flex;
    flex-direction: column;
    font-size: 16px;
    align-items: center;
    gap: 30px;
    margin-top: 30px;
    background-color: hsl(33, 100%, 50%, 5%);
    border-radius: 10px;
    padding: 20px;
    width: 80%;
    border: 1px solid #F0A952;
}

.responseCardSections {
    display: flex;
    flex-direction: row;
    width: 90%;
    gap: 30px;
}

.responseBoxBoard {
    width: 50%;
    max-height: 700px;
    display: flex;
    align-items: center;
    margin-right: 10px;
    overflow-y: auto;
}

.responseBoxBoardRight {
    width: 50%;
    max-height: 700px;
    overflow: hidden;
    overflow-y: auto;
    padding-right: 15px;
    border-radius: 5px;
}

.responseImage {
    width: 100%;
}

.titleInformationBox {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
}

.divider {
    height: 1px;
    width: 100%;
    background-color: #F0A952;
}

.actionPostButtons {
    display: flex;
    flex-direction: row;
    gap: 30px;
}

.actionButton {
    width: 100px;
    padding: 10px 0px 10px 0px;
    border-radius: 5px;
    background-color: #FF8C00;
}

.actionButton:hover {
    background-color: #E8BD88;
    cursor: pointer;
}